.project.component__space {
  padding-top: 100px;
  padding-bottom: 200px;
}
.project {
  background-color: #191919;
}

img.project__img {
  border-radius: 10px;
  width: 100%;
  object-fit: cover;
  transition: .5s;
  opacity: .7;
  position: relative;
}

img.project__img::after{
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgb(250, 250, 250);
  z-index: 1;
  opacity: 1;
}

.mask__effect {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(44, 0, 14);
  border-radius: 10px;
  transition: .4s;
  opacity: .6;
  display: none;
}

.project__box:hover .mask__effect{
  display: inline;
  transform: scale(1.1);
}

.project__box:hover img.project__img{
  transform: scale(1.1);
}

.project__meta.absolute {
  top: 0%;
  left: 0%;
  display: flex;
  height: 90%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  border-radius: 10px;
  width: 100%;
}

h5.project__text {
  font-weight: 400;
  font-size: 20px;
  padding-bottom: 20px;
}

h4.project__text {
  font-size: 22px;
  font-family: Poppins, sans-serif;
  padding-bottom: 18px;
  font-weight: 700;
}

a.project__btn {
  padding: 12px 35px;
  border: 2px solid #ffffff87;
  border-radius: 5px;
  transition: .4s ease;
}

a.project__btn:hover {
  background-color: #f9004d;
  border: 2px solid #f9004d;
}

button.btn {
  padding: 15px 20px;
  font-size: 18px;
  font-weight: 500;
  border-radius: 5px;
  border: 2px solid #f9004d;
  background-color: #f9004d;
  color: #fff!important;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: .4s all ease;
}

button.view__more:hover {
  background-color: #fff;
  border: 2px solid #f9004d;
  color: #f9004d!important;
}

/* RESPONSIVE */
@media only screen and (max-width: 768px){
  h5.project__text {
    font-weight: 400;
    font-size: 20px;
    padding-bottom: 20px;
  }
  h4.project__text {
    font-size: 18px;
  }
  
}
